	import topBar from '../../components/TopBar.vue'
	import mInput from '../../components/m-input.vue'
	import ButtonItems from "../../components/ButtonItems.vue"
    export default {
		data(){
			return{
				cardsInfo:{},
				cardNumber:'',
				amount:''
			}
		},
		components: {
			topBar,
			mInput,
			ButtonItems
		},
		onLoad() {
			this.getCardsInfo()	
		},
        methods: {
			setNumber(event){
				this.amount = event
			},
            getCardsInfo(){
				this.request({
					url: 'consumer/my/bank-cards',
				}).then(res => {
					this.cardsInfo = res.data.result
					let num = res.data.result.cardNumber
					this.cardNumber = num.substr(num.length-4)
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '信息获取失败，请稍后重试',
					});
				})
			},
			submitHandle(){
				if(this.amount <= 0 ){
					uni.showToast({
						icon: 'none',
						title: '请输入正缺的充值金额！',
					});
					return false
				}
				this.request({
					url: 'consumer/my/recharge-records',
					params:{
						amount: this.amount,
						callbackUrl:'/pages/common/success'
					}
				}).then(res => {
					console.log(res)
					// window.location.href="../../confirmStorage.html"
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '数据提交失败，请稍后重试',
					});
				})
			}
        }
    }
